<template>
	<div class="page about-section white-bg p-65px-t">
		<div class="container-fluid p-0">
			<div class="row no-gutters">
				<div class="col-lg-2 col-xl-3 page-title bg-contact-us-image">
					<div class="v-center-box d-flex align-items-end">
						<h2>{{ header }}</h2>
					</div>
				</div>
				<div class="col-lg-10 col-xl-9">
					<div class="page-scroll">
						<div class="page-content">
							<div class="section-titel">
								<div class="st-title">
									<h2 class="theme-after dark-color">{{ header }}</h2>
								</div>
							</div>
							<div class="row">
								<div class="col-md-12">
									<div class="about-text">
										<section v-html="body"></section>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	const fb = require("@/firebase/Firebase");

	export default {
		data () {
			return {
				lang : { 
					en: {},
					fr: {}
				}
			}
		},
		created: function (argument) {
			fb.db.collection('languages').get().then((row) => {
				row.forEach((doc) => {
					this.lang = doc.data().configs;
				});
			});
		},
		computed: {
			header: function() {
				return this.lang[this.$i18n.locale].aboutHeader;
			},
			body: function() {
				return this.lang[this.$i18n.locale].aboutText;
			}
		},
		methods: {
		}
	}	
</script>